<template>
	<view class="content">
			<view class="text-box" scroll-y="true">
				<text>自定义文本</text>
			</view>
			<view class="uni-list">
				<view class="uni-list-cell uni-list-cell-pd">
					<view class="uni-list-cell-db">左移滚动/右移滚动</view>
					<switch @change="switch1Change" />
				</view>
				<view class="uni-list-cell uni-list-cell-pd">
					<view class="uni-list-cell-db">单行显示/双行显示</view>
					<switch @change="switch2Change" />
				</view>
			</view>
			<view class="uni-list">
				<view class="uni-list-cell uni-list-cell-pd">
					<input class="uni-input" v-model="myText" type="text" placeholder="支持英文与部分中文字符" />
				</view>
				<view class="uni-list-cell uni-list-cell-pd" v-show="rowSwitch">
					<input class="uni-input" v-model="myText2" type="text" placeholder="第二行输入" />
				</view>
			</view>
			<button type="default" @click="generateText" style='margin-top: 30rpx;'> 数据发送 </button>
	</view>
</template>

<script>
	export default {
		data() {
			return {
				myText: "",
				myText2: "",
				leftSwitch: false,
				rowSwitch: false,
			}
		},
		onLoad() {

		},
		methods: {
			switch1Change: function (e) {
				console.log('switch1 发生 change 事件，携带值为', e.target.value)
				this.leftSwitch = e.target.value;
				
			},
			switch2Change: function (e) {
				console.log('switch2 发生 change 事件，携带值为', e.target.value)
				this.rowSwitch = e.target.value;
			},
			generateText: function(){
				let that = this;
				uni.openBluetoothAdapter({
				  success: function (res) {
					uni.showToast({
						icon: "loading",
						title: '正在搜索设备'
					})
					//开始搜索硬件设备
					uni.startBluetoothDevicesDiscovery({
					  // services: ["20:DF:26:A8:CB:76"],
					  allowDuplicatesKey: false,
					  success(e) {
						console.log('开始搜索蓝牙设备:' + e.errMsg);
					  }
					})
					uni.onBluetoothDeviceFound(devices => {
						// console.log(devices.devices)
						if(devices.devices[0].deviceId === "B4:10:7B:C4:76:B3"){
							console.log('发现设备');
							let deviceId = "B4:10:7B:C4:76:B3";
							uni.showToast({
								title: '正在连接蓝牙...',
								icon: 'loading',
								duration: 9000
							});
							//连接蓝牙
							uni.createBLEConnection({
								deviceId,
								success: res => {
									console.log(res);
									console.log('连接蓝牙成功:' + res.errMsg);
									// 连接设备后断开搜索 并且不能搜索设备
									uni.stopBluetoothDevicesDiscovery();
									uni.hideToast();
									uni.showToast({
										title: '连接成功',
										icon: 'success',
										duration: 2000
									});
									function sleep (time) {
									  return new Promise((resolve) => setTimeout(resolve, time));
									}
									sleep(1000).then(async () => {
										uni.showToast({
											title: '正在进行数据传输...',
											icon: 'loading',
											duration: 5000
										});
										let serviceId = "0000FFE0-0000-1000-8000-00805F9B34FB";
										let characteristicId = "0000FFE1-0000-1000-8000-00805F9B34FB";
										// var bf = Uint8Array.from(new Uint8Array(64).keys())
										let textStr=" 0123456789abcdefghijklmnopqrstuvwxyzABCDEFGHIJKLMNOPQRSTUVWXYZ,!.张晓雪、周嘉文，电子信息与通信学院。"
										let textIndex = []
										let ctr = 0
										if(!that.leftSwitch) ctr+=0xF0;
										if(that.rowSwitch) ctr+=0x0F;
										textIndex.push(ctr)
										if(!that.rowSwitch){
											for(let i = 0; i < that.myText.length; ++i){
												textIndex.push(textStr.indexOf(that.myText[i]))
											}
										}
										else{
											let maxLen = Math.max(that.myText.length, that.myText2.length);
											let font1 = (maxLen - that.myText.length)/2;
											let font2 = (maxLen - that.myText2.length)/2;
											for(let i = 0; i < maxLen; ++i){
												if(i < font1 || i-font1 >= that.myText.length) textIndex.push(0)
												else textIndex.push(textStr.indexOf(that.myText[i-font1]))
												if(i < font2 || i-font2 >= that.myText2.length) textIndex.push(0)
												else textIndex.push(textStr.indexOf(that.myText2[i-font2]))
											}
										}
										textIndex.push(0x0D)
										textIndex.push(0x0A)
										console.log(textIndex)
										var bf = Uint8Array.from(new Uint8Array(textIndex));
										// bf.push(Uint8(0x0D))
										// bf.push(Uint8(0x0A))
										console.log(bf);
										let i = 0;
										while(i + 20 <= bf.length){
											var tempBf = bf.slice(i, i+20);
											await uni.writeBLECharacteristicValue({
											  deviceId,
											  serviceId,
											  characteristicId,
											  value: tempBf
											})
											i = i + 20;
										}
										if(i < bf.length){
											var tempBf = bf.slice(i);
											await uni.writeBLECharacteristicValue({
											  deviceId,
											  serviceId,
											  characteristicId,
											  value: tempBf
											})
										}
										uni.showToast({
											title: '数据传输成功',
											icon: 'success',
											duration: 2000
										});
										uni.closeBLEConnection({
										  deviceId,
										  success(res) {
											console.log('蓝牙已断开')
										  }
										})
									})
								},
								fail: e => {
									console.log('连接蓝牙失败，错误码：' + e.errCode);
									uni.hideToast();
									uni.showToast({
										title: '连接失败，请重试',
										icon: 'none',
										duration: 2000
									});
								}
							});
						}
					})
				  },
				  fail: function (e) {
					uni.showToast({
						icon: "none",
						title: '初始化蓝牙失败，错误码：' + (e.errCode + '\n' + e.errMsg),
						duration: 2000
					})
				  },
				  
				})
			}
		}
	}
</script>

<style>
	.uni-list {
		background-color: #FFFFFF;
		position: relative;
		width: 100%;
		display: flex;
		flex-direction: column;
		margin-top: 20rpx;
		margin-bottom: 40rpx;
	}
	.uni-list-cell {
		position: relative;
		display: flex;
		flex-direction: row;
		justify-content: space-between;
		align-items: center;
	}
	.uni-list-cell-pd {
		padding: 22rpx 30rpx;
	}
	.text-box {
		margin-bottom: 40rpx;
		margin-top: 20rpx;
		padding: 40rpx;
		display: flex;
		min-height: 30rpx;
		background-color: #FFFFFF;
		justify-content: center;
		align-items: center;
		text-align: center;
		font-size: 30rpx;
		color: #353535;
		line-height: 1.8;
	}
	.uni-input {
		height: 50rpx;
		padding: 15rpx 25rpx;
		line-height:50rpx;
		width: 80%;
		font-size:28rpx;
		background:#d3d3d3;
		flex: 1;
	}
	.content {
		display: flex;
		flex-direction: column;
		align-items: center;
		justify-content: center;
	}

	.logo {
		height: 200rpx;
		width: 200rpx;
		margin-top: 200rpx;
		margin-left: auto;
		margin-right: auto;
		margin-bottom: 50rpx;
	}

	.text-area {
		display: flex;
		justify-content: center;
	}

	.title {
		font-size: 36rpx;
		color: #8f8f94;
	}
</style>
